// aws lambda 호출하여 계산 결과 가져오기
function call(data, onLoad) {
  fetch(
    "https://li6ctgphqglz6ol7cuo5qik3dm0mphxc.lambda-url.us-east-1.on.aws/",
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: window.btoa(window.JSON.stringify(data)),
    }
  )
    .then((response) => response.json())
    .then((data) => onLoad(data));
}

export default call;
